<template>
  <br />
  <v-card flat hover color="primary">
    <v-card-title class="d-flex align-center pe-2">
      <v-icon icon="mdi-toggle-switch-outline"></v-icon> &nbsp; Toggle it to
      enable/disable

      <v-spacer></v-spacer>
    </v-card-title>

    <v-divider></v-divider>
    <v-table fixed-header density="comfortable" hover>
      <v-skeleton-loader v-if="!dataLoaded" type="table-tbody"></v-skeleton-loader>
      <thead v-if="dataLoaded">
        <tr>
          <th class="text-left">Partner Code</th>
          <th class="text-left">Apply</th>
          <th class="text-left">Purchase</th>
          <th class="text-left">Payment Calc</th>
          <th class="text-left">Lookup</th>
        </tr>
      </thead>
      <tbody v-if="dataLoaded">
        <tr v-for="(item, index) in partnerDetails" :key="item.code">
          <td>{{ item.partner_code }}</td>
          <td>
            <ToggleComponent
              :toggle="item.apply == 'Y' ? true : false"
              type="apply"
              :index="index"
              :code="item.partner_code"
              @changeStatus="handleToggle"
            />
          </td>
          <td>
            <ToggleComponent
              :toggle="item.purchase == 'Y' ? true : false"
              type="purchase"
              :index="index"
              :code="item.partner_code"
              @changeStatus="handleToggle"
            />
          </td>
          <td>
            <ToggleComponent
              :toggle="item.calc == 'Y' ? true : false"
              type="calc"
              :index="index"
              :code="item.partner_code"
              @changeStatus="handleToggle"
            />
          </td>
          <td>
            <ToggleComponent
              :toggle="item.lookup == 'Y' ? true : false"
              type="lookup"
              :index="index"
              :code="item.partner_code"
              @changeStatus="handleToggle"
            />
          </td>
        </tr>
      </tbody>
    </v-table>
  </v-card>
</template>

<script setup>
import ToggleComponent from "@/components/ToggleComponent.vue";
import { ref } from "vue";
import { usePartnerStore } from "@/store/PartnerStore.js";
import supabase from "@/supabase/supabase_client.js";
import { useToast } from "vue-toastification";
const toast = useToast();

const partnerStore = usePartnerStore();
const partnerDetails = ref([]);
const dataLoaded = ref(false);

const getData = async () => {
  try {
    console.log("inside getData");
    const { data, error } = await partnerStore.getCacheData;
    if (error) throw error;
    partnerDetails.value = data;
    console.log("partnerDetails.value", partnerDetails.value);
    dataLoaded.value = true;
  } catch (error) {
    console.warn(error);
  }
};

getData();

const handleToggle = async (props) => {
  console.log("props -->", props, props.toggle, props.type, props.code);
  partnerDetails.value[props.index][props.type] = !props.toggle == true ? "Y" : "N";
  const data = await updateDataBase(props);
  console.log("update data", data);
  if (props.toggle) {
    toast.success(`${props.type} is enabled for partner code ${props.code}`);
  } else {
    toast.warning(`${props.type} is disabled for partner code ${props.code}`);
  }
};

const updateDataBase = async (props) => {
  try {
    let index = props.index + 1;
    let val = !props.toggle == true ? "Y" : "N";
    console.log("val --->", val);
    if (props.type == "apply") {
      const { data, error } = await supabase
        .from("partner_lookup")
        .upsert({
          id: index,
          apply: val,
        })
        .select();
      if (error) throw error;
      console.log(data);
      return data;
    } else if (props.type == "purchase") {
      const { data, error } = await supabase
        .from("partner_lookup")
        .upsert({
          id: index,
          purchase: val,
        })
        .select();
      if (error) throw error;
      console.log(data);
      return data;
    } else if (props.type == "calc") {
      const { data, error } = await supabase
        .from("partner_lookup")
        .upsert({
          id: index,
          calc: val,
        })
        .select();
      if (error) throw error;
      console.log(data);
      return data;
    } else if (props.type == "lookup") {
      const { data, error } = await supabase
        .from("partner_lookup")
        .upsert({
          id: index,
          lookup: val,
        })
        .select();
      if (error) throw error;
      console.log(data);
      return data;
    }
  } catch (error) {
    console.log(error);
  }
};
</script>
