import { defineStore } from "pinia";
import { useToast } from "vue-toastification";
import supabase from "@/supabase/supabase_client.js";

export const usePartnerStore = defineStore({
  id: "partner",
  state: () => ({
    partnerDetails : []
  }),
  actions: {
    updateFlag(type, index, value) {
      const toast = useToast();
      console.log(type, index);
    },
    updatePartnerState(data) {
      console.log("data to be updated", data)
      if (data) {
          this.partnerDetails = [];
          this.partnerDetails.push(data);
        }
      console.log('state updated', this.partnerDetails)
    },
  },
  getters: {
    async getCacheData() {
      try {
        const { data, error } = await supabase
          .from("partner_lookup")
          .select()
          .order("id", { ascending: true });
        if (error) throw error;
        console.log("data in state", data);
        //this.updatePartnerState(data);
        return {data, error};
      } catch (error) {
        console.warn(error);
      }
    },
  },
});
